import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";
import Detail from "./routes/Detail";
import Home from "./routes/Home";

function App() {
  return (
  <Router>
    <Switch>  {/* Switch Route . Route    URL . Route    */}
      <Route path="/hello">
        <h1>Hello</h1>  {/**    */}
      </Route>

      <Route path="/movie/:id"> {/** /:id   id .      */}
        <Detail />
      </Route>
      
      <Route path="/"> {/*    . path URL     */}
        <Home />  {/**   */}
      </Route>      
    </Switch>
  </Router>
  );
}
export default App;